<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">选择家长</p>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <div class="pl-30" style="border-bottom:.06rem solid #f1f1f1">
                <img :src="checkedAll?checked:notCheck" @click="selectAll">
                <span>发送给全部家长</span>
            </div>
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index">
                    <img :src="item.checked?checked:notCheck" @click="isChecked(index)">
                    <span>{{item.name}}</span>
                </li>
            </ul>
        </div>
        <div class="centerBtn f26 cfff text-center" @click="issue">确 认</div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            checkedAll:false,
            notCheck:require('../../assets/message/notCheck.png'),
            checked:require('../../assets/message/checked.png'),
            list:[
                {
                    checked:false,
                    name:'张一鸣家长',
                },{
                    checked:true,
                    name:'黄大仙家长',
                },{
                    checked:true,
                    name:'刘大为家长',
                },{
                    checked:false,
                    name:'杨露家长',
                },{
                    checked:true,
                    name:'张易女家长',
                },{
                    checked:false,
                    name:'谢丰家长',
                },{
                    checked:false,
                    name:'金正家长',
                },{
                    checked:false,
                    name:'丁麒麟家长',
                }, 
            ]
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        issue(){

        },
        isChecked(index){
            var list=this.list;
            list[index].checked=!list[index].checked;

            var btn=true;
            for(var i in list){
                if(!list[i].checked){
                    btn=false;
                }
            }
            this.checkedAll=btn;
            this.list=list;
            
        },
        selectAll(){
            this.checkedAll=!this.checkedAll;
            var list=this.list;
            if(this.checkedAll){
                for(var i in list){
                    list[i].checked=true;
                }
            }else{
                for(var i in list){
                    list[i].checked=false;
                }
            }
            this.list=list;
            
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
    .content{
        line-height: 1rem;
    }
    .content img{
        width:.4rem;
        height:.4rem;
        margin-right: .2rem;
    }
</style>




